<template>
  <div class="hello" style="width:80%;display: flex;">
    <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="100px" class="demo-dynamic">

      

      
  <el-form-item
    prop="rqname"
    label="容器名"
    :rules="[
      { required: true, message: '请输入容器名', trigger: 'blur' }
    ]"
  >

    <el-input v-model="dynamicValidateForm.rqname"></el-input>
  </el-form-item>
  <el-form-item
    prop="rqid"
    label="容器ID"
    :rules="[
      { required: true, message: '请输入容器ID', trigger: 'blur' }
    ]"
  >


    <el-input v-model="dynamicValidateForm.rqid"></el-input>
  </el-form-item>
  
      <el-form-item
    v-for="(domain, index) in dynamicValidateForm.dkys"
    :label="'端口映射' + (index+1)"
    :key="domain.key"
    :prop="'domains.' + index + '.value'"
  >
<div style="display:flex;">
      <el-input v-model="domain.value"></el-input>
      <el-input v-model="domain.value2" style="margin-left: 2%;"></el-input>
    <el-button @click.prevent="removeDkys(domain)" style="margin-left: 2%;">删除</el-button>
</div>


  </el-form-item>


  <el-form-item
    v-for="(domain, index) in dynamicValidateForm.mlys"
    :label="'目录映射' + (index+1)"
    :key="domain.key"
    :prop="'domains.' + index + '.value'"
  >
<div style="display:flex;">
      <el-input v-model="domain.value"></el-input>
      <el-input v-model="domain.value2" style="margin-left: 2%;"></el-input>
    <el-button @click.prevent="removemlys(domain)" style="margin-left: 2%;">删除</el-button>
</div>


  </el-form-item>


  
  <el-form-item>
    <el-button type="primary" @click="submitForm('dynamicValidateForm')">提交</el-button>
    <!-- <el-button @click="addDomain">新增端口</el-button> -->
    <el-button @click="adddkys">新增端口映射</el-button>
    <el-button @click="addmlys">新增目录映射</el-button>
    <!-- <el-button @click="resetForm('dynamicValidateForm')">重置</el-button> -->
  </el-form-item>

  <div>
  <el-row>
  <el-col>
    <el-card shadow="always">
      {{ docker }}
    </el-card>
  </el-col>
</el-row>
</div>
</el-form>



  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data() {
      return {
        dynamicValidateForm: {
          domains: [{
            value: '',
            value2:''
          }],
          
          email: '',
          mlys:[{
            value: '',
            value2:''
          }],
          dkys:[{
            value: '',
            value2:''
          }],
          rqid:'',
          rqname:''
        },
        docker:"请填入需求"
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {

            let DockerShell = "docker run -dit";
            // 拼接端口映射
            let dk = "-p ";
            let dks = "";

            var dkysArr = [];
            this.dynamicValidateForm.dkys.forEach( element=> {
              
              dkysArr.push(dk + element.value+":"+element.value2);
            });
            
            var dkysStr = dkysArr.join(" ");


            let ml = "-v ";
            let mls = "";

            var mlysArr = [];
            this.dynamicValidateForm.mlys.forEach( element=> {
              
              mlysArr.push(ml + element.value+":"+element.value2);
            });
            
            var mlysStr = mlysArr.join(" ");


            this.docker = DockerShell+" "+dkysStr+" "+mlysStr+" "+"-name "+this.dynamicValidateForm.rqname+" "+this.dynamicValidateForm.rqid;

          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      removeDomain(item) {
        var index = this.dynamicValidateForm.domains.indexOf(item)
        if (index !== -1) {
          this.dynamicValidateForm.domains.splice(index, 1)
        }
      },

      removeDkys(item) {
        var index = this.dynamicValidateForm.dkys.indexOf(item)
        if (index !== -1) {
          this.dynamicValidateForm.dkys.splice(index, 1)
        }
      },
      
      removemlys(item) {
        var index = this.dynamicValidateForm.mlys.indexOf(item)
        if (index !== -1) {
          this.dynamicValidateForm.mlys.splice(index, 1)
        }
      },
      
      addDomain() {
        
        this.dynamicValidateForm.domains.push({
          value: '',
          key: Date.now()
        });


      },

            
      addmlys() {
        
        this.dynamicValidateForm.mlys.push({
          value: '',
          key: Date.now()
        });


      },

      adddkys() {
        this.dynamicValidateForm.dkys.push({
          value: '',
          key: Date.now()
        });
      }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
